@mixin product-button()
    padding: 5px 12px 5px 10px
    margin: 5px
    width: 50px
    height: 50px
    border-radius: 5px
    background-color: white
    background-size: 90%
    background-repeat: no-repeat
    background-position: center
    transition: all 0.2s ease-in-out

    &:hover
        box-shadow: 0 0 15px rgba(0, 123, 255, 0.5)
        transform: scale(1.1)
    
*
    margin: 0
    box-sizing: border-box

.--hidden
    display: none
    pointer-events: none

.main-nav--container
    width: 100%
    border-bottom: 3px solid black
    padding-bottom: 10px

    > h1
        text-align: center
        font-size: 50px

    > nav
        width: fit-content
        margin: 0 auto
        font-size: 30px
        font-weight: bold

#product-list
