.product-entry
    position: relative
    margin: 10px
    width: 300px
    height: 300px
    border: 1px solid black
    border-radius: 0 5px 5px 5px
    overflow: hidden
    transition: box-shadow 0.2s ease-in-out

    &:hover
        box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.2)

    a
        position: relative
        display: block
        width: 100%
        height: 100%

        *
            pointer-events: none

        img
            display: block
            position: absolute
            width: 75%
            height: 75%
            margin: 20% 12.5% 5%
            object-fit: contain

        h3
            display: block
            position: absolute
            padding: 5px 10px
            z-index: 1
            width: 100%
            text-align: center
            background-color: white
            border-bottom: 1px solid black
    
    .product-entry-price
        position: absolute
        bottom: 10px
        left: 10px
        z-index: 1
        min-width: 60px
        text-align: center
        font-size: 20px
        font-weight: bold
        background-color: white
        border: 1px solid black
        border-radius: 0 5px 5px 5px
        padding: 5px
        pointer-events: none

    .product-entry-buttons
        position: absolute
        display: grid
        grid-template-columns: 60px 60px
        bottom: 10px
        right: 10px
        z-index: 1

        .add-to-cart
            @include product-button()
            color: white
            font-weight: bold
            text-align: right
            font-size: 15px

