.cart-entry
    .product-entry-buttons
        display: grid
        grid-template-rows: auto auto

    .cart-entry-fields
        display: grid
        justify-self: end
        grid-column: span 2
        grid-template-columns: 1fr 1fr

        .cart-entry-fields-sum
            font-size: 15px
            text-align: center
            margin: 5px 5px
            padding: 3px 5px
            border: 1px solid black
            border-radius: 0 5px 5px 5px
            background-color: white

        .cart-entry-fields-amounts
            width: 50px
            font-size: 15px
            margin: 5px 5px
            padding: 3px 0 3px 10px
            border: 1px solid black
            border-radius: 0 5px 5px 5px

            &::-webkit-inner-spin-button
                cursor: pointer
                margin: -3px 0

#modal
    position: fixed
    top: 0
    left: 0
    width: 100vw
    height: 100vh
    background-color: #5555
    z-index: 1

    #modal-container
        position: absolute
        top: 50%
        left: 50%
        transform: translate(-50%, -50%)
        max-width: 90%
        width: 800px
        min-height: 500px
        border: 1px solid black
        border-radius: 0 5px 5px 5px
        background-color: white

        #modal--close-button
            @include product-button()
            position: absolute
            top: 0
            right: 0
            background-image: url("data:image/svg+xml,%3Csvg%20width%3D%22800%22%20height%3D%22800%22%20viewBox%3D%220%200%2016%2016%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20fill%3D%22none%22%3E%3Cpath%20fill%3D%22%235559%22%20d%3D%22M12.78%204.28a.75.75%200%200%200-1.06-1.06L8%206.94%204.28%203.22a.75.75%200%200%200-1.06%201.06L6.94%208l-3.72%203.72a.75.75%200%201%200%201.06%201.06L8%209.06l3.72%203.72a.75.75%200%201%200%201.06-1.06L9.06%208z%22%2F%3E%3C%2Fsvg%3E")


.product-details-header
    margin: 15px
    border-bottom: 1px solid black

    h2
        margin: auto
        padding-right: 75px
    h3
        margin: auto
        padding-right: 75px
.product-details-body
    display: grid
    grid-template-columns: 1fr 1fr
    margin: 15px

    img
        width: 300px
        max-width: 30vw
        max-height: 50vh
        aspect-ratio: auto
        margin: auto
    p
        margin: auto